import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {BrowserRouter, Link, Switch, Route} from "react-router-dom"
import Mountains from "./components/mountain"
import Beaches from "./components/beaches"
import Birds from "./components/birds"
import Food from "./components/food"
import Search from './components/search';

function App() {
  return(
    <div className="container">
      <div className="searchResult">
        <Search/>
      </div>      
      <div class="btnContainer">
        <Link to= "/mountains"><button>Mountains</button></Link>
        <Link to ="/beaches"><button>Beaches</button></Link>
        <Link to ="/birds"><button>Birds</button></Link>
        <Link to ="/food"><button>Food</button></Link>
      </div>
      <Switch>
        <Route path="/mountains" >{<Mountains />}</Route>
        <Route path="/beaches" >{<Beaches />}</Route>
        <Route path="/birds" >{<Birds />}</Route>
        <Route path="/food" >{<Food />}</Route>
      </Switch>
    </div>
  )
}

ReactDOM.render(<BrowserRouter>,<App></App>,</BrowserRouter>,document.getElementById("root"))