import React from "react"
import {Link,Route} from "react-router-dom"
import "../index.css"

class Search extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      results: null,
      loading: false,
      header: null
    }
    this.fetchSearchResults = this.fetchSearchResults.bind(this)
  }

  fetchSearchResults ()  {
    var input = document.querySelector("#searchInput").value;
    var head = input.toUpperCase() + " PICTURES"
    console.log(input)
    const url = `https://www.flickr.com/services/rest/?method=flickr.photos.search&api_key=fcfe983fc8be195ed8cb18d28e8b98aa&tags=` + input + `&format=json&nojsoncallback=1` 

    fetch(url)
      .then(response => response.json())
      .then(data  => {
        let picArray = data.photos.photo.map((pic,index) => {
          var srcPath = "https://live.staticflickr.com/"+ pic.server + "/" + pic.id + "_" + pic.secret + ".jpg";
          return(
            <div className="searchImage">
              <img src= {srcPath}  alt="loading..."></img>
            </div>
          )
        })
        this.setState({
          results: picArray,
          loading: false,
          header:  head
        })
      })

      .catch(err => {
        console.log(err)
      })
  };



 render() {
   return(
    <div className="searchContainer">
        <div className="inputAndSearch">
            <input type="text" id="searchInput" placeholder="Search...."></input>
            <Link className= "link" to="/search"><button className="onClickBtns" onClick={this.fetchSearchResults}>Search</button></Link>
            <Route exact path="/search" ></Route>
        </div>  
        <div class="results">
            <div className="header">
                <h2>{this.state.header}</h2>
            </div> 
            <div className="imgResults">
                {this.state.results}
            </div>              
        </div>
    </div>
   )
 }
} 

export default Search                                                                                                                                                           