import React from "react"
import "../index.css"


class Mountain extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      results: null,
      loading: false,
      header: null
    }
  }

  componentDidMount ()  {
    var head = "Mountain pictures"
    const url = `https://www.flickr.com/services/rest/?method=flickr.photos.search&api_key=fcfe983fc8be195ed8cb18d28e8b98aa&tags=mountain&format=json&nojsoncallback=1` 

    fetch(url)
      .then(response => response.json())
      .then(data  => {
          console.log(data)
        let picArray = data.photos.photo.map((pic) => {
          var srcPath = "https://live.staticflickr.com/"+ pic.server + "/" + pic.id + "_" + pic.secret + ".jpg";
          return(
            <div className="searchImage">
              <img src= {srcPath}  alt="loading..."></img>
            </div>
          )
        })
        this.setState({
          results: picArray,
          loading: false,
          header: head
        }).bind(this)
      })

      .catch(err => {
        console.log(err)
      })
  };



 render() {
   return(
    <div className="searchContainer">
        <div class="results">
          <div className="header">
            <h2>{this.state.header}</h2> 
          </div>
          <div className="imgResults">
            {this.state.results}
          </div>  
       </div>
    </div>
   )
 }
} 

export default Mountain